import React, { useEffect, useState } from 'react';
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import './App.css';

function App() {
  function getjoke(first,second){
    fetch(`http://api.icndb.com/jokes/random?${first}=John&lastName=${second}`)
    .then(res=>res.json())
    .then(res2=>{
      console.log(res2)
      setJoke(res2.value.joke)
    })
  }
  const [jokes,setJoke]=useState("loading");
  const[fname,setfn]=useState("john");
  const[lname,setln]=useState("singh");
  useEffect(()=>{
  
         getjoke(fname,lname);
  },[fname,lname])
  return (
    <div className="App">
    <div className="center_div">
    <h3>Jokes Fetching App </h3>
    <div  className="inputbox">
    <input  type="text"value={fname}onChange={(e)=>setfn(e.target.value)}/>
    <input  type="text"value={lname}onChange={(e)=>setln(e.target.value)}/>
    </div>
     <h3>{jokes}</h3>
     </div>
    </div>
  );
}

export default App;
